<template>
    <div class="system">
        <breadcrumb :name="name"></breadcrumb>
        <div class="search-box">
            <search v-if="options.length > 0" class="search-keywords" :options="options" @option-change="optionChange"></search>
            <span class="execl-in">
                <a @click="showForm" class="button">导入</a>
            </span>
            <search-input class="search-keywords"></search-input>
        </div>
        <tablebox :theads="theads" class="table-box" @thClick="sortTable">
            <tr v-for="(value, key, index) in tables" v-show="tables.id[index]">
                <td v-for="(th, num) in theads">
                    {{tables[th][index]}}
                </td>
            </tr>
        </tablebox>
        <pagination v-if="totalPages > 0" :showPages="10" :totalPages="totalPages" @pageChange="pageChange"></pagination>
    </div>
</template>

<script>
import breadcrumb from 'components/breadcrumb'
import tablebox from 'components/table-box'
import { arrSplice } from 'common/js/dom'
import pagination from 'components/pagination'
import search from 'components/search'
import searchInput from 'components/search-input'

import { getCurrentTable } from 'api/getTable'
import { getSeoKeywords } from 'api/seo'
import { getTotalPages } from 'api/getPages'
import { getSystemPageUrl, getSystemSortUrl, getSystemOriginUrl, getSystemCodenameUrl } from 'common/js/apiUrl.js'


export default {
    data() {
        return {
            name: 'SEO 对应码表',
            options: [],
            optionsId: [],
            totalPages: 0,
            selectedOption: 10001,
            selectedPage: 1,
            selectedHead: 'id',
            direction: 'asc',
            theads: ['id', 'infoCode', 'value', 'codeName', 'rank'],
            tables: {
                'total': 0,
                'id': [],
                'infoCode': [],
                'value': [],
                'codeName': [],
                'rank': []
            },
            big: 3
        }
    },
    created() {
        getSeoKeywords(getSystemOriginUrl(), this.options, this.optionsId)
        getCurrentTable(this.pageUrl, this.theads, this.tables)
        getTotalPages(getSystemOriginUrl()).then((res)=>{
            this.totalPages = res.data.total
        })
    },
    computed: {
        codeNameUrl() {
            return getSystemCodenameUrl(this.selectedOption)
        },
        pageUrl() {
            return getSystemPageUrl(this.selectedPage, this.selectedOption)
        },
        sortUrl() {
            return getSystemSortUrl(this.selectedHead, this.direction, this.selectedOption)
        }
    },
    watch: {
        selectedOption(val) {
            getCurrentTable(this.codeNameUrl, this.theads, this.tables)
        },
        selectedPage(val) {
            console.log(this.pageUrl)
            getCurrentTable(this.pageUrl, this.theads, this.tables)
        },
        sortUrl(val) {
            getCurrentTable(val, this.theads, this.tables)
        }
    },
    components: {
        breadcrumb,
        pagination,
        search,
        searchInput,
        tablebox
    },
    methods: {
        pageChange(el) {
            this.selectedPage = el
        },
        optionChange(el) {
            this.selectedOption = this.optionsId[el]
        },
        showForm() {
            this.$router.push({
                name: 'file',
                params: { codeId: this.selectedOption }
            })
        },
        sortTable(el) {
            this.selectedHead = el
            this.direction = this.direction === 'desc' ? 'asc' : 'desc'

            console.log(this.selectedHead)
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable'
.system
    display : flex
    flex-direction : column
    min-height : 100%
    font-size: 14px
    .search-box
        display : flex
        align-items : center
        padding: 10px
        .search-keywords
            flex: 1
        .execl-in
            flex: 1
            .button
                display: inline-block;
                outline: none;
                cursor: pointer;
                text-align: center;
                text-decoration: none;
                color : #fff
                background : green
                padding: 10px 20px;
                text-shadow: 0 1px 1px rgba(0,0,0,.3);
                border-radius: 6px;
                box-shadow: 0 1px 2px rgba(0,0,0,.2);
                &:hover
                    cursor : pointer
                    background : #51a351
                &:active
                    position: relative
	                top: 1px
    .table-box
        flex : 1
        margin-bottom : 10px
        tr
            display : flex
            td
                flex : 1
                padding : 8px
                line-height : 20px
                text-align : left
                border-bottom : 1px solid $color-input-border
                &.big
                    flex: 2
</style>



